<script>
import { mapGetters } from "vuex";
import Step1 from "../../components/catalog-steps/Step1";

export default {
  name: "ServiceCatalog",
  components: {
    Step1,
  },
  data() {
    return {
      current: 0,
      steps: [
        {
          title: "First",
          content: "First-content",
        },
        {
          title: "Second",
          content: "Second-content",
        },
        {
          title: "Last",
          content: "Last-content",
        },
      ],
    };
  },
  methods: {
    next() {
      this.current++;
    },
    prev() {
      this.current--;
    },
  },
  computed: {
    ...mapGetters(["catalogData"]),
  },
  mounted() {
    this.$store.dispatch("callGetServiceCatalog");
  },
