<template>
  <div>
    <a-form :form="form" style="max-width: 500px; margin: 40px auto 0;">
      <a-form-item
        label="Service Catalog"
        :labelCol="labelCol"
        :wrapperCol="wrapperCol"
      >
        <a-select
          @change="handleChange"
          placeholder="Select service catalog"
          v-decorator="[
            'serviceCatalog',
            {
              rules: [
                {
                  required: true,
                  message: 'Please select any service catalog',
                },
              ],
            },
          ]"
        >
          <a-select-option
            v-for="item in catalogData"
            v-bind:key="item.id"
            :value="item.id"
            >{{ item.name }}</a-select-option
          >
        </a-select>
      </a-form-item>
    </a-form>
    <a-divider />
    <a-form-item :wrapperCol="{ span: 19, offset: 5 }">
      <a-button type="primary" @click="nextStep">Next</a-button>
    </a-form-item>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { dialog } from "../../utilities/constants";
export default {
  name: "Step1",
  data() {
    return {
      labelCol: { lg: { span: 5 }, sm: { span: 5 } },
      wrapperCol: { lg: { span: 19 }, sm: { span: 19 } },
      form: this.$form.createForm(this),
    };
  },
  methods: {
    nextStep() {
      const {
        form: { validateFields },
      } = this;

      validateFields((err, values) => {
        if (!err) {
          console.log("form: ", this.form);
          this.$emit("nextStep");
        } else {
          console.log("Go!");
        }
      });
    },
    handleChange(value) {
      this.$store.commit("updateSelectedCatalogId", value);
    },
  },
  computed: {
    ...mapGetters(["catalogData"]),
  },
  mounted() {
    // Just for user reference for dialog heirarchy dialog => tabs => groups => fields => field
    dialog.dialog_tabs.forEach((tab, tab_index) => {
      // tabs
      tab.dialog_groups.forEach((group, group_index) => {
        // groups
        group.dialog_fields.forEach((field, field_index) => {
          // fields

          const fieldValue = field.default_value;
          dialog.dialog_tabs[tab_index].dialog_groups[
            group_index
          ].dialog_fields[field_index].default_value = fieldValue;
        });
      });
    });
    console.log("dialog : ", dialog);
    this.$store.dispatch("callGetServiceCatalog");
  },
};
</script>

